@import "../../styles/styles.scss";

.background {
  background-color: $background-cream;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 4rem 0;

  @include desktop {
    background-color: $background-cream;
    background-size: cover;
    background-repeat: no-repeat;
    padding-bottom: 3%;
  }

  // background-position: 0% 100%;
}

.description {
  font-size: $font-size-2;
  color: $primary-darker;
  font-family: $font-main;
  font-weight: 400;
  text-align: center;
  margin-bottom: 4rem;
}

.container {
  display: flex;
  justify-content: space-evenly;
  flex-flow: row wrap;
  width: 100%;
  padding: 0% 15%;


  .button {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    color: $primary-dark;
    font-family: $font-main;
    margin: 5%;

    >img {
      height: 12rem;
      padding-bottom: 8%;

      &:hover {
        cursor: pointer;
      }

      &:hover~div {
        background-color: $light-blue;
        -webkit-transition: background-color 0.3s;
      }

    }

    >div {
      font-size: $font-size-3;
      text-align: center;
      border-radius: 200px;
      padding: 4% 10%;
      width: 90%;
      -webkit-transition: background-color 0.3s;
      background-color: rgba(0, 0, 0, 0);

      &:hover {
        cursor: pointer;
        background-color: $light-blue;
        -webkit-transition: background-color 0.3s;
      }
    }

  }




}

.heading {
  display: flex;
  flex-direction: column;
  justify-content: center;

  h2 {
    text-align: center;
    color: $primary-dark;
    @include phone{
      font-size: $font-size-4;

    }
    @include desktop {
      text-align: center;
      font-weight: bolder;
      font-family: $font-title;
      font-size: $font-size-5;
    }


  }
}

.content {
  display: flex;
  justify-content: space-around;

  .resources_item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 10%;
    padding-bottom: 15%;

    @include desktop {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-left: 10%;
    }

  }

  width: 70%;
  display: flex;
  flex-direction: column;

  h3 {
    font-weight: normal;
    font-size: $font-size-2;
  }

  @include desktop {
    flex-direction: row;
  }
}

.textbox {
  margin: 2rem;
  padding: 1rem;
  width: 250px;
  height: 250px;
  background-color: $accent-pink;
  text-align: center;
  vertical-align: middle;
  box-shadow: 7px -7px $stream-blue;
}

.iconContainer {
  font-size: $font-size-5;
  color: $primary-dark;
  margin-bottom: 1rem;
}

.text {
  margin-bottom: 2rem;
}